const t = TrelloPowerUp.iframe();

const finish = () => {
  t.closePopup();
};

window.addYear = () => {
  t.cards('all').then((cards) => {
    console.log(JSON.stringify(cards, null, 2));
  });
  finish();
};

window.removeYear = () => {
  alert('remove year');
  finish();
};

window.unsetCompleted = () => {
  alert('unset completed');
  finish();
};

window.closePopup = () => {
  finish();
};

t.render(() => {
  t.sizeTo('#edit-date').done();
});
